import { IHandledIndicatorTreeData, IIndicatorTreeData, ISelectedIndicator } from '../types';
import { CN_WORD_REGEX } from './regex';

/** 获得总共指标列情况 */
export const getTotalIndicator = (treeData: IIndicatorTreeData[] | IHandledIndicatorTreeData[]) => {
  let count = 0;

  const countNum = (item: IIndicatorTreeData) => {
    if (item?.children?.length) {
      item?.children?.forEach(countNum);
    } else {
      count += 1;
    }
  };
  treeData.forEach(countNum);
  return count;
};

/** 得到表格列树所有节点key */
export const getAllTreeNodeKeys = (treeData: IIndicatorTreeData[]) => {
  return treeData.reduce((arr, cur) => {
    if (cur?.children) {
      cur?.children && arr.push(...cur?.children);
    }
    return arr;
  }, [] as ISelectedIndicator[]);
};

/** 获取长度title呈现长度 */
export const getWordShowLength = (word: string, fontSize: number) => {
  const wordArr = new Array(word?.length);
  return [...wordArr].reduce((len, cur) => {
    if (cur && CN_WORD_REGEX.test(cur)) {
      len += fontSize;
    } else {
      len += Math.ceil(fontSize / 2) + 3;
    }
    return len;
  }, 0);
};
