
import React, { useState } from 'react';
import SetColumns, { ISelectedIndicator, ISetColumns } from '@global/materials/setcolmns'
import { DEFAULT_SELECTED_COLUMNS, DEFAULT_TREE_DATE } from './common';
type ISetColumnsOmit = Omit<ISetColumns, 'defaultTreeData' | 'defaultSelectedList'>;
interface ISetColumnsWrap extends ISetColumnsOmit {

}

export function SetColumnsWrap(props: ISetColumnsWrap) {
  const { buttonName, ...resSetColumnsProps } = props;
  /** 当前已选择列 */
  const [curSelectedList, setCurSelectedList] = useState<ISelectedIndicator[]>(DEFAULT_SELECTED_COLUMNS);

  /** 获取更新后的表格列相关信息 */
  const handleUpdateViewDetail = (selectedIndicator: any, indicatorTreeData: any) => {
    console.log('updateViewDetail', selectedIndicator, indicatorTreeData);
    setCurSelectedList(selectedIndicator);
  };
  console.log('curSelectedList', curSelectedList);

  return (
    <div>
      <SetColumns
        buttonName={buttonName || "设置表头"}
        buttonType="link"
        setColumnType="modal"
        updateViewDetail={handleUpdateViewDetail}
        defaultSelectedList={DEFAULT_SELECTED_COLUMNS}
        defaultTreeData={DEFAULT_TREE_DATE}
        {...resSetColumnsProps}
      />
    </div>
  )
}
