import React from 'react';

import { Tabs } from 'antd';
import s from './mgt.m.scss';
import GridLayout from './modules/grid-layout';
import RoleMgt from './modules/role-mgt';
// const GridLayout = import('./modules/grid-layout');
// const RoleMgt = import('./modules/role-mgt')

enum EKeyMapComp {
  RoleMgt,
  GridLayout,
}

function getCompByKey(key: EKeyMapComp) {
  switch (key) {
    case EKeyMapComp.RoleMgt:
      return <RoleMgt />;
    case EKeyMapComp.GridLayout:
      return <GridLayout />;
  }
}
interface IPageMgt {
  pageName: string;
}
export function PageMgt(props: IPageMgt) {
  const { pageName = 'pageName' } = props ?? {};

  return (
    <div className={s.wrap_mgt}>
      <div className={s.wrap_tabs}>
        <h2>{pageName}</h2>
        <Tabs
          defaultActiveKey="0"
          tabPosition={'top'}
          onTabClick={(activeKey: string, e: React.KeyboardEvent<Element> | React.MouseEvent<Element, MouseEvent>) => {
            // console.log('activeKey', activeKey)
          }}
          items={new Array(30).fill(0).map((_, i) => {
            const id = String(i);
            return {
              label: `Tab-${id}`,
              key: id,
              disabled: i === 28,
              children: <>
                {i % 2 === 0 ? getCompByKey(EKeyMapComp.RoleMgt) : getCompByKey(EKeyMapComp.GridLayout)}
              </>,
            };
          })}
        />
      </div>
    </div>
  )
}
