import React, { Key, useRef } from 'react';
import {
  arrayMove,
  SortableContainer as sortableContainer,
  SortableElement as sortableElement,
  SortableHandle as sortableHandle,
} from 'react-sortable-hoc';

import classNames from 'classnames';

import { ISelectedIndicator } from '../../types';
import { useClassnames } from '../../utils';

import { CloseIcon, DragIcon, LockIcon } from '../Icons';

interface IDraggableOrderProps {
  /**
   * 进行拖拽排序的列表
   */
  list: ISelectedIndicator[];
  updateList: (newIndicator: ISelectedIndicator[]) => void;
  className?: string;
  onDeleteItemClick: (key: Key) => void;
}
const DraggableOrder = (props: IDraggableOrderProps) => {
  const { list, className, updateList, onDeleteItemClick } = props;

  const cn = useClassnames();

  const DragHandle = sortableHandle(({ itemkey, title }: any) => {
    return (
      <div key={itemkey}>
        <div className={classNames(cn('Draggbleorder-list-item'), cn('Draggbleorder-list-item-move'))}>
          <DragIcon className={cn('Draggbleorder-drag-handle')} />
          <span className={cn('Draggbleorder-drag-title')}>{title}</span>

          <span
            onClick={() => {
              onDeleteItemClick(itemkey);
            }}>
            <CloseIcon className={cn('Draggbleorder-drag-close')} />
          </span>
        </div>
      </div>
    );
  });

  const SortableItem = sortableElement(({ lock, title, itemkey }: any) => {
    return lock ? (
      <div key={itemkey}>
        <div className={cn('Draggbleorder-list-item')}>
          <LockIcon className={cn('Draggbleorder-drag-lock')} />
          <span className={cn('Draggbleorder-drag-title')}>{title}</span>
        </div>
      </div>
    ) : (
      <DragHandle itemkey={itemkey} title={title} />
    );
  });

  // eslint-disable-next-line @typescript-eslint/no-shadow
  const SortableList = sortableContainer(({ list }: any) => {
    /** 将 lock 为 true 的排序到前面 */
    // const lockFirst = (a: ISelectedIndicator, b: ISelectedIndicator) => (a.lock ? (b.lock ? 0 : -1) : b.lock ? 1 : 0);
    return (
      <div>
        {list?.map(({ lock, title, key }: ISelectedIndicator, index: number) => (
          <SortableItem
            disabled={lock}
            // 限制拖动分类，加上后只能统一类别才能拖动，如同为lock的才能拖动
            // collection={lock ? 'lock' : 'normal'}
            itemkey={key}
            key={key}
            index={index}
            title={title}
            lock={lock}
          />
        ))}
      </div>
    );
  });

  const container = useRef(null);
  const onSortEnd = ({ oldIndex, newIndex }: any) => {
    updateList(arrayMove(list, oldIndex, newIndex));
  };

  return (
    <div className={classNames(cn('Draggbleorder'), className)}>
      <div className={cn('Draggbleorder-list-container')} ref={container}>
        <SortableList
          // 设置大于0可点击到内部
          distance={1}
          helperClass={cn('helperSort')}
          lockToContainerEdges
          lockOffset={['50%', '50%']}
          onSortEnd={onSortEnd}
          lockAxis="y"
          list={list}
          useDragHandle
          helperContainer={() => container.current}
        />
      </div>
    </div>
  );
};

export default DraggableOrder;
