import React from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import SafeImg from './components/safe-img';
import { Tooltip } from 'antd';
import HoverCoupon from './components/hover-coupon';
import DefaultHover from './components/hover-default';
import { PmtCard4KolPc, MainProductTag, Hover } from './type';
import styles from './index.module.scss';

export enum HasLabelAction {
  CLICK = 'click',
  HOVER = 'hover',
  NO_ACTION = 'noAction',
}

// 小标签hover后展示内容
const renderTagsTooltip = (hover = {} as Hover, border_color?: string, type?: string) => {
  const { coupon_hover = [], normal_hover } = hover;

  switch (type) {
    case 's1':
      return <HoverCoupon coupon_hover={coupon_hover} border_color={border_color} />;
    default:
      return <DefaultHover normal_hover={normal_hover} />;
  }
};

// 小标签hover的背景颜色
const renderTextTagsHoverBgColor = (hover = {} as Hover, type?: string) => {
  const { normal_hover = {} } = hover;
  return '#fff';
};



// 大标签配置化，优先级后端控制
export const renderPicProductTags = (tags = {} as PmtCard4KolPc, hasAction?: HasLabelAction) => {
  const { pic_product_tag = [] } = tags;

  if (!pic_product_tag?.length) {
    return null;
  }

  return pic_product_tag.map((item: MainProductTag) => {
    const { pic, height = 16, width, action = {}, type, text, border_color } = item || {};
    const { click } = action;

    if (pic) {
      return (
        <div
          key={pic}
          className={styles['label-configuration_pic']}
          style={{ height }}
          onClick={() => {
          }}
        >
          <SafeImg url={pic} height={height} width={width} />
        </div>
      );
    }

    if (text) {
      const { text: mainText, color, bg_color } = text;
      const lineStyle = border_color ? { border: `1px solid ${border_color}` } : {};

      return (
        <span
          className={styles['label-configuration_pd4']}
          style={{ color: color, background: bg_color, ...lineStyle }}
        >
          {mainText}
        </span>
      );
    }

    return null;
  });
};

// 小标签配置化，优先级后端控制
export const renderTextProductTags = (tags = {} as PmtCard4KolPc, hasAction?: HasLabelAction) => {
  const { text_product_tag = [] } = tags;

  if (!text_product_tag?.length) {
    return null;
  }

  return text_product_tag?.map((item: MainProductTag, index: number) => {
    const { pic, text = {}, text_right = {}, height, border_color, action = {}, type } = item || {};
    const { text: mainText, color, bg_color } = text;
    const { text: rightText, color: rightColor, bg_color: rightBgColor } = text_right;
    const borderStyle = { border: `1px solid ${border_color}` };
    const lineStyle = { borderLeft: `0.5px dashed ${border_color}` };

    const keys = Object.keys(item);

    if (!keys?.length || !(mainText || rightText || pic)) {
      return null;
    }

    return (
      <div
        key={`${index}-${mainText}`}
        className={styles['label-configuration_text']}
        style={mainText ? borderStyle : {}}
      >
        <div
          className={classNames(
            styles['label-configuration_text_main'],
            mainText && styles['label-configuration_text_pd4'],
            rightText && styles['label-configuration_text_pd3']
          )}
          style={{ color: color, background: bg_color }}
        >
          <Tooltip
            title={
              hasAction === HasLabelAction.HOVER && action?.hover
                ? renderTagsTooltip(action?.hover, border_color, type)
                : ''
            }
            color={renderTextTagsHoverBgColor(action?.hover)}
          >
            {pic && (
              <div
                style={{ height }}
                className={classNames(
                  styles['label-configuration_text_main_pic'],
                  mainText && styles['label-configuration_text_main_mr']
                )}
              >
                <SafeImg url={pic} height={height} />
              </div>
            )}
          </Tooltip>
          <span>{mainText}</span>
        </div>
        {/* 劵类型非hover右侧展示部分 */}
        {rightText ? (
          <div className={styles['label-configuration_text_right']}>
            <div className={styles['label-configuration_text_line']} style={lineStyle} />
            <span
              className={styles['label-configuration_text_right_text']}
              style={{ color: rightColor, background: rightBgColor }}
            >
              {rightText}
            </span>
          </div>
        ) : null}
      </div>
    );
  });
};
