// }
export function touchKeyToList<T extends IDataSourceSelect>(list: T[], baseLen: number) {
  return list?.map((item: T, idx: number) => {
    if (!isNil(item?.key)) {
      return item;
    } else {
      return {
        ...item,
        key: idx + baseLen,
      }
    }
  });
}
const promotionMax = 2000;
export function SelectGoodsSelf() {
  const [cards, setCards] = useState<IDataSourceSelect[]>([]);
  const [hasMore, setHasMore] = useState(true);
  const [loading, setLoading] = useState(false);
  const [loadingFailed, setLoadingFailed] = useState(false);
  const [formValues, setFormValues] = useState({});

  const fetchMore = throttle((init = false) => {
    console.log('scroll fetch')
    setLoading(true);
    getSomeGoodsList({ page: 1, page_size: 40, range: 20 }).then(res => {
      const originCards = cards;
      if (originCards?.length > promotionMax) {
        setHasMore(false)
      }
      if (!init) {
        setCards([...originCards, ...touchKeyToList(res as IDataSourceSelect[], originCards?.length)])
      } else {
        setCards([...touchKeyToList(res as IDataSourceSelect[], 0)]);
      }
    }).catch(err => {
      setLoadingFailed(true);

    }).finally(() => {
      setLoading(false);
    })
  }, 600)

  const refStart = useRef(null);
  const refEnd = useRef(null)
  const cellSize = useSize(refStart);
  console.log('cellSize', cellSize, loading)
  const scrollEventRef = useRef(null);
  const { totalHeight, visibleHeight, getTransform, visibleList, scrollEvent } = useVirtualScroll<IDataSourceSelect>({
    list: cards,
    containerEle: scrollEventRef.current,
    scrollEventRef: scrollEventRef as React.MutableRefObject<any>,
    refStart: refStart as React.MutableRefObject<any>,
    refEnd: refEnd as React.MutableRefObject<any>,
    cellHeight: cellSize?.height || 470,
    cellWidth: cellSize?.width || 200
  });
  function getCurRef(idx: number) {
    if (0 === idx) {
      return refStart;
    } else if (visibleList.length - 1 === idx) {
      return refEnd;
    }
    return null;
  }

  useEffect(() => {
    fetchMore(true);
    return () => {
      setCards([]);
    }
  }, [formValues]);

  // const SkeletonList = visibleList?.slice(0, Math.max(8, visibleList.length))?.map((card: IDataSourceSelect, idx: number) => {
  //   return (
  //     <Skeleton loading={loading} active>
  //       <div className='prefix_flux_card_wrap'
  //         ref={idx === 0 ? refStart : undefined}
  //         key={card?.key}>
  //         <GoodsCard
  //           goodsInfo={card as IDataSourceSelect}
  //           showDis={true}
  //           isFixPromotion={true}
  //           handleDistribution={() => {
  //             console.log('goods', card)
  //           }}
  //         />
  //       </div>
  //     </Skeleton>
  //   )
  // })

  function getCurRef(idx: number) {
    if (0 === idx) {
      return refStart;
    } else if (visibleList.length - 1 === idx) {
      return refEnd;
    }
    return null;
  }

  return (
    <div className='select_goods_wrap'>
      <SearchForm searchCallback={(values) => {
        setFormValues(values)
      }} />
      <div id='prefix_scrollContainer_wrap'>
        <div ref={scrollEventRef} onScroll={scrollEvent} className='prefix_scrollContainer'>
          <div style={{ height: totalHeight + 'px' }} className='infinite_list_phantom'></div>
          <div style={{ height: visibleHeight + 'px' }} className='infinite_list_visible'></div>
          <div id={'containerRef'} style={{ transform: getTransform }} className='infinite_list'>
            <Spin spinning={loading} delay={1000}>
              <div className='prefix_flux_card_list' >
                <div id={'topObserver'}></div>
                {visibleList?.map((card: IDataSourceSelect, idx: number) => {
                  return (
                    <div className='prefix_flux_card_wrap'
                      ref={getCurRef(idx)}
                      key={card?.key}>
                      <GoodsCard
                        goodsInfo={card as IDataSourceSelect}
                        showDis={true}
                        isFixPromotion={true}
                        handleDistribution={() => {
                          console.log('goods', card)
                        }}
                      />
                    </div>
                  )
                })}

                {/* <div id={'footerObserver'}></div> */}
                <LoadMore
                  hasMore={hasMore}
                  isFetching={loading}
                  isFetchFailed={loadingFailed}
                  onActive={fetchMore}
                  onRetry={fetchMore}
                  fetchBuffer={cellSize?.height || 100}
                  options={{
                    root: document.getElementById('prefix_scrollContainer_wrap'),
                    rootMargin: '-670px 0px 0px 670px',
                  }}
                // fetchingText={<CircleLoading />}
                />
              </div>

            </Spin>
          </div>

        </div>
      </div>

    </div>
  )
}
