import { message } from 'antd';
import React, { useCallback } from 'react';

import Clipboard from 'clipboard';
import Icon, { CopyOutlined } from '@ant-design/icons';

export const CopyId = React.memo(({ id, message: _message }: { id: string; message: string }) => {
  const copy = useCallback((_id: string) => {
    const a = document.createElement('a');
    const clipBtn = new Clipboard(a, {
      text: () => _id,
    });
    clipBtn.on('success', () => {
      message.success(_message);
    });
    a.click();
  }, []);

  return (
    <div>
      <span style={{ marginRight: 7 }}>{id}</span>
      <Icon
        onClick={e => {
          e.stopPropagation();
          copy(id);
        }}
        component={CopyOutlined }
        style={{ cursor: 'pointer' }}
      />
    </div>
  );
});
