import React from 'react';
import { NormalHover } from '@apis/_ferry/api-lib/ecom.kol.selection_pc_api/promotion';

interface Iprops {
  normal_hover?: NormalHover;
  border_color?: string;
}

export default (props: Iprops) => {
  const { normal_hover = {} } = props;

  const { text: hoverList } = normal_hover;

  if (!hoverList?.length) {
    return null;
  }

  return (
    <div>
      {hoverList.map((item, index) => {
        const { url, text, color } = item;
        return (
          <div key={`${index}-${text}`} style={{ color }}>
            {url ? (
              <a
                href={url}
                target="_blank"
                rel="noopener noreferrer"
                onClickCapture={e => {
                  e.stopPropagation();
                }}
                style={{ color }}
              >
                {text}
              </a>
            ) : (
              text
            )}
          </div>
        );
      })}
    </div>
  );
};
