import numeralChain, { ApproximateType } from '@global/materials/numeral-chain';
import { isNil } from 'lodash';

// 数据概览中的格式化
// 转换数据概览中的金钱
// 输入->输出：1234.5->["¥","1,234.5",""] 1234.567->["¥","1,234.57",""] 12345678->["¥","1234.6","万"] 123456789->["¥","1.2","亿"]
export function transferPriceToArray(num?: number | string): string[] {
  if (isNil(num) || (typeof num === 'number' && isNaN(num))) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 9999.995, 99999500], scales: ['', '/10000', '/100000000'] })
    .choiceFormat(['0,0.[00]', '0.[0]', '0.[0]'])
    .addPrefix('¥')
    .addSuffix(['', '万', '亿'])
    .toArr();
}

// 转换数据概览中的数量
// 输入->输出：1234.5->["","1,235",""] 12345678->["","1234.6","万"] 123456789->["","1.2","亿"]
export function transferAmountToArray(num?: number | string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 9999.5, 99999500], scales: ['', '/10000', '/100000000'] })
    .choiceFormat(['0,0', '0.[0]', '0.[0]'])
    .addSuffix(['', '万', '亿'])
    .toArr();
}

// 转换数据概览中的百分比
// 输入->输出：0.1->["","10","%"] 0.123456->["","12.35","%"] 0.112->["","11.2","%"]
export function transferPercentageToArray(num?: number | string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num).scale('*100').choiceFormat('0.[00]').addSuffix('%').toArr();
}

// 表格中的格式化
// 转换表格中的金钱
// 输入->输出：1234.5->["¥","1,234.50",""] 1234.567->["¥","1,234.57",""] 12345678->["¥","1234.6","万"] 123456789->["¥","1.2","亿"]
export function transferTablePriceToArray(num?: number | string): string[] {
  if (isNil(num) || isNaN(Number(num))) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 9999.995, 99999500], scales: ['', '/10000', '/100000000'] })
    .choiceFormat(['0,0.00', '0.0', '0.0'])
    .addPrefix('¥')
    .addSuffix(['', '万', '亿'])
    .toArr();
}

// 转换表格中的数量
// 输入->输出：1234.5->["","1,235",""] 12345678->["","1234.6","万"] 123456789->["","1.2","亿"]
export function transferTableAmountToArray(num?: number | string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 9999.5, 99999500], scales: ['', '/10000', '/100000000'] })
    .choiceFormat(['0,0', '0.0', '0.0'])
    .addSuffix(['', '万', '亿'])
    .toArr();
}

// 转换表格中的百分比
// 输入->输出：0.1->["","10.00","%"] 0.123456->["","12.35","%"] 0.112->["","11.20","%"]
export function transferTablePercentageToArray(num?: number | string, choiceFormat?: string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  const newChoiceFormat = choiceFormat || '0.00';
  return numeralChain(num).scale('*100').choiceFormat(newChoiceFormat).addSuffix('%').toArr();
}

// 转换分数为字符串
// 输入->输出：1.1->"1.10" 3.1234->"3.12"
export function transferScoreToStr(num?: number | string): string {
  if (isNil(num)) {
    return '-';
  }
  return numeralChain(num).choiceFormat('0.00').toString();
}

// 格式化分数
// 输入->输出：1.123344->1.12 3->3
export function transferScore(num?: number | string): string {
  if (isNil(num)) {
    return '-';
  }
  return numeralChain(num).choiceFormat('0.[00]').toString();
}

// 数字格式：截断保留一位小数(不四舍五入)
// 输入->输出：1234.5->['','1,234',''] 1234.567->['','1,234',''] 123456->['','12.3','万'] 12345678->['','1234.5','万'] 1256789->['','125.6','万'] 123456789->['','1.2','亿']
export function transferNumCutFloorToArr(num?: number | string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 10000, 100000000], scales: ['', '/10000', '/100000000'] })
    .choiceFormat(['0,0', '0.[0]', '0.[0]'], ApproximateType.FLOOR)
    .addSuffix(['', '万', '亿'])
    .toArr();
}

// 转换数据概览中的数量
// 输入->输出：1234.5->["","1,235",""]
export function transferAmountToArrayWithoutSuffix(num?: number | string): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num).choiceFormat('0,0.[0]').toArr();
}

export function transferPriceToArrayWithoutSuffix(num?: number | string, isCent = false, precisely = false): string[] {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .choiceFormat(precisely ? '0,0.00' : '0,0.[00]')
    .scale({ minLimits: [0], scales: [isCent ? '/100' : ''] })
    .addPrefix('¥')
    .toArr();
}

// 131313 -> 13w 1234 -> 1234
export function transferNumToWSuffix(num?: number | string) {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  return numeralChain(num)
    .scale({ minLimits: [0, 10000], scales: ['', '/10000'] })
    .choiceFormat(['0', '0'])
    .addSuffix(['', 'w'])
    .toArr();
}

// 将分钟转换成x小时
export const minuteToHour = (minute?: number) => {
  if (isNil(minute)) {
    return ['', '-', ''];
  }

  return numeralChain(minute).scale('/60').choiceFormat('0.[0]').addSuffix('小时').toArr();
};

// 格式化次数、人数
export const transferTimes = (num?: number | string, unit = '次') => {
  if (isNil(num)) {
    return ['', '-', ''];
  }
  const amount = transferAmountToArray(num);
  return ['', amount[1], `${amount[2]}${unit}`];
};

// 将分钟转换成x分钟
export const minuteToMinute = (minute?: number) => {
  if (isNil(minute)) {
    return ['', '-', ''];
  }

  return numeralChain(minute).choiceFormat('0.[0]').addSuffix('分').toArr();
};

export const formatCountDownNumber = (num: number) => numeralChain(num).choiceFormat('00').toString();
