import React, { useCallback, useMemo } from 'react';

// import { LoginForm } from 'components/login-form';
import { SearchForm, TValues } from 'components/search-form';
import { Button, Empty, Table, Tabs, message } from 'antd';
// import { TabsFilter } from 'components/tabs/tabs-filter';
import { IDataSource } from '@global/pages/page-mgt/model/type';
import { GetColumns } from './GetColumns';
import { ColumnProps } from 'antd/es/table';
import { useRecoilState, useRecoilValue } from 'recoil';
import { productAtom, sedProdsAtom } from '@global/pages/page-mgt/model/select-goods';

import s from './index.m.scss';
import { Operation } from '@global/components/operation';
import { SetColumnsWrap } from '../set-columns-wrap';

export default function RoleMgt() {
  const [selectedProds, setSelectedProds] = useRecoilState(sedProdsAtom)
  const onSearch = (values: TValues) => {
    console.log('formValues', values)
  }
  const product_list = useRecoilValue(productAtom);
  const operation: ColumnProps<IDataSource> = {
    title: 'operation',
    width: 88,
    align: 'left',
    fixed: 'right',
    render: (_: string, rd: IDataSource): JSX.Element => {
      return (
        <div>
          <Button
            type={'link'}
            onClick={() => {
              message.warning('view');
            }}
          >
            view
          </Button>
          <Button
            type={'link'}
            onClick={() => {
              message.warning('copy');
            }}
          >
            copy
          </Button>
          <Button
            type={'link'}
            onClick={() => {
              message.warning('delete');
            }}
          >
            delete
          </Button>
          <Button
            type={'link'}
            onClick={() => {
              message.warning('edit');
            }}
          >
            edit
          </Button>
        </div>
      );
    },
  };
  // console.log('selectedProds', selectedProds, !selectedProds?.length)
  const getBatchOpDisabled = () => {
    return !selectedProds?.length;
  }

  const operationsSlot: Record<string, React.JSX.Element> = useMemo(() => ({
    left: <Button className="tabs-extra-demo-button">Left Action</Button>,
    right: (
      <>
        <Button
          disabled={getBatchOpDisabled()}
          onClick={() => {
            message.success('batch delete success')
          }}
        >
          batch delete
        </Button>
        <SetColumnsWrap
          buttonName='set indicator self'
          setColumnType="drawer"
        />
      </>
    ),
  }), [selectedProds])

  return (
    <div className={s.wrap}>
      <SearchForm searchCallback={onSearch} />
      <section className={s.operation_wrap}>
        <Operation operationsSlot={operationsSlot} />
      </section>
      <Table
        id={'FindGoodsTable'}
        className={`custom-style-gray`}
        rowClassName="row-vertical-top"
        columns={GetColumns<IDataSource>(operation)}
        dataSource={product_list}
        scroll={{ x: 'max-content' }}
        rowKey={(rd: IDataSource, index?: number) => rd.product_id}
        // loading={tableLoading}
        sticky={{
          offsetHeader: 64,
        }}
        locale={{
          emptyText: (
            <Empty image={''} imageStyle={{ height: 222, width: '100%' }} description="">
              <span style={{ color: '#85878A' }}>{'暂无搜索结果'}</span>
            </Empty>
          ),
        }}
        rowSelection={{
          fixed: true,
          defaultSelectedRowKeys: [],
          // selectedRowKeys: selectedProds,
          onSelect: (record: IDataSource, selected: boolean, selectedRows: IDataSource[], nativeEvent) => {
            // console.log('onSelect', record, selected, selectedRows, nativeEvent);
          },
          onChange: (selectedRowKeys: string[], selectedRows: IDataSource[], info: { type }) => {
            // console.log('onChange', selectedRowKeys, selectedRows, info);
            setSelectedProds(selectedRowKeys)
          }
        }}
      />
    </div>
  )
}
