import { parse, stringify } from 'qs';

// 函数式的方式，避免微前端场景导致的状态缓存（直接返回对象会有缓存）。
export function getQuery<T = { [index: string]: string }>() {
  const search = location.search.slice(1);
  return parse(search) as T;
}

/**
 * 给地址 path 上添加参数。例如: /path/a?key=value
 * @param path: string - 地址
 * @param params: Record<string, any> - 附加参数
 * @returns string - 添加参数后的 path
 */
export const setURLParam = (path: string, param: Record<string, any>): string => {
  const [base, search] = path.split('?');
  const query = Object.assign(parse(search || ''), param);
  const newPath = `${base}?${stringify(query)}`;
  return newPath;
};

/**
 * 把window.location.href转换成对象
 * @param params: window.location.href
 * @returns {hash: "", host: "buyin-boe.bytedance.net", origin: 'xxx', search: 'xxx', ...}
 */
function parseUrl(url: string) {
  const newUrl = url.startsWith('//') ? location.protocol + url : url;
  return new window.URL(newUrl);
}

/**
 * 删除url中指定的参数
 * @param param: 要删除的参数名 如productId，如果有多个 用英文逗号分隔 eg. productId,type
 * @returns {hash: "", host: "buyin-boe.bytedance.net", origin: 'xxx', search: 'xxx', ...}
 */
export function removeUrlParam(param: string, url = window.location.href) {
  const obj = parseUrl(url);
  const params = param.split(',');
  params.forEach((item: string) => {
    obj.searchParams.delete(item);
  });
  return obj.href;
}
