
import { PmtCard4KolPc } from 'components/goods/picking-goods-card/types';
import selectGoodsJson from '../mock/goods.json';
export interface IGoodsList {
  page?: number;
  page_size?: number;
  range?: number;
}
export interface IDataSourceSelect extends PmtCard4KolPc {
  key: React.Key;
}
export function getSomeGoodsList(props?: IGoodsList): Promise<PmtCard4KolPc[]> {
  const { page = 1, page_size = 10, range = 20 } = props ?? {};
  const { promotions = [] } = selectGoodsJson?.data ?? {};
  const res = [];
  for (let i = 0;i < page_size;i++) {
    const element = promotions[i % range];
    res.push(element);
  }
  return new Promise((resolve, reject) => {
    resolve(res as PmtCard4KolPc[]);
  });
}