
import { useDebounceFn, useSize } from 'ahooks';
import { useEffect, useMemo, useState } from 'react';
import { IDataSourceSelect } from 'src/pages/daren/select-goods/model/apis';

type TContainerEle = HTMLElement;
interface IUseVirtualScroll<T> {
  cellHeight: number;
  cellWidth: number;
  containerEle: TContainerEle;
  scrollEventRef: React.MutableRefObject<any>;
  refStart: React.MutableRefObject<any>;
  refEnd: React.MutableRefObject<any>;
  list: T[];
  cols?: number;
  bufferFloor?: number;
  threshold?: number;
  /**
   * gap
   */
  gap?: number;
  rowGap?: number;
}

export function useVirtualScroll<T extends IDataSourceSelect>(props: IUseVirtualScroll<T>) {
  const { list, cellWidth = 200, cellHeight = 10, containerEle, scrollEventRef, refStart, refEnd, cols = 8, bufferFloor = 4, gap = 4, rowGap = 16 } = props ?? {};
  const [screenHeight, setScreenHeight] = useState(0);
  const [floorCols, setFloorCols] = useState(cols);
  const [bufferStart, bufferEnd] = [bufferFloor, bufferFloor];
  const itemHeight = cellHeight;
  const size = useSize(scrollEventRef);
  const cellSize = useSize(refStart);

  useEffect(() => {
    if (size?.width && cellSize?.width) {
      const _cellWidth = cellSize?.width ? cellSize.width : cellWidth;
      const _cols = Math.round(size.width / (_cellWidth + gap));
      setFloorCols(_cols);
    }
  }, [size?.width, cellSize?.width])

  const visibleFloor = useMemo(() => {
    return Math.ceil(screenHeight / itemHeight) + bufferEnd;
  }, [screenHeight, itemHeight, containerEle, bufferEnd, bufferStart]);

  //此时的开始索引 //此时的结束索引
  const [virtualIdx, setVirtualIdx] = useState({
    start: 0,
    end: visibleFloor,
    startOffset: 0,
  });
  const { startOffset } = virtualIdx;
  const visibleList = useMemo(() => {
    const itemStart = virtualIdx.start * floorCols;
    const itemEnd = virtualIdx.end * floorCols;
    // 根据 `virtualIdx.start` 和 `virtualIdx.end` 以及 `floorCols` 来更新 visibleList
    return list.slice(itemStart, Math.min(itemEnd, list.length));
  }, [virtualIdx.start, virtualIdx.end, list]);

  const totalHeight = useMemo(() => Math.ceil(list.length * itemHeight / floorCols), [list.length, itemHeight, floorCols]);
  const visibleHeight = useMemo(() => Math.ceil(visibleList.length * itemHeight / floorCols), [visibleList.length, itemHeight, floorCols])
  const getTransform = useMemo(() => {
    return `translate3d(0,${startOffset}px,0)`;
  }, [startOffset]);
  const { run: scrollEvent } = useDebounceFn(function () {
    //当前滚动位置
    const scrollTop = containerEle?.scrollTop;
    //此时的开始索引
    const startIdx = Math.floor(scrollTop / itemHeight);
    const visibleIdx = startIdx + bufferStart;
    const curStartIdx = startIdx;

    const resItemHeight = scrollTop % itemHeight;
    // const bufferStartH = itemHeight * visibleFloor / floorCols;

    const calcOffset = scrollTop - resItemHeight;
    if (startOffset !== calcOffset) {
      setVirtualIdx({
        start: curStartIdx,
        //此时的结束索引
        end: visibleIdx + visibleFloor,
        //此时的偏移量
        startOffset: calcOffset,
      })
    }
  }, { wait: 100 });

  // useEffect(() => {
  //   if (containerEle) {
  //     containerEle.addEventListener('scroll', scrollEvent);
  //   }
  //   return () => {
  //     containerEle.removeEventListener('scroll', scrollEvent)
  //   }
  // }, [])



  useEffect(() => {
    if (containerEle?.clientHeight && screenHeight !== containerEle?.clientHeight) {
      setScreenHeight(containerEle?.clientHeight);
    }
  }, [containerEle]);

  return {
    totalHeight,
    visibleHeight,
    visibleFloor,
    // visibleCount,
    getTransform,
    visibleList,
    scrollEvent: scrollEvent,
  }
}
