import { Tabs } from 'antd';
import React from 'react';
import { SubAppBorder } from '@global/styles/layout/sub-app-border';
import { StepNumAuto } from '@global/materials/animate/number-auto/numeral';
import { EnterBg } from '@global/materials/animate/enter-bg';
import { AnimateEasyBg } from '@global/materials/animate/easy-bg';

import { AnimateMenuTree } from '@global/materials/animate/tree';

enum ETechCssMapComp {
  CircleLoading,
}
const techAnimate = [
  <StepNumAuto />,
  <EnterBg />,
  <AnimateMenuTree />,
  <AnimateEasyBg />
]
function getCompByKey(idx: number) {
  return techAnimate?.[idx];
}

export default function TechAnimate() {
  return (
    <SubAppBorder>
      <h2>{'TechAnimate'}</h2>
      <Tabs
        defaultActiveKey="0"
        tabPosition={'top'}
        onTabClick={(activeKey: string, e: React.KeyboardEvent<Element> | React.MouseEvent<Element, MouseEvent>) => {
          // console.log('activeKey', activeKey)
        }}
        items={new Array(30).fill(0).map((_, i) => {
          const id = String(i);
          return {
            label: `Tab-${id}`,
            key: id,
            disabled: i === 28,
            children: <div style={{ minWidth: `calc(80vw)`, minHeight: `calc(80vh)` }}>
              {getCompByKey(i)}
            </div>,
          };
        })}
      />
    </SubAppBorder>
  )
}
