import { floor, isNil } from 'lodash';
/**
 * 千分位
 * @param n 数字
 */
export const toTenPercentile = (n: number | string): string => {
  const parts = n.toString().split('.');
  // eslint-disable-next-line @ecom/security-unsafe-regex
  parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  return parts.join('.');
};
// type TValueType = 'int' | 'floor';
// 向下取整(截断)保留传入位数小数
export function toFixFloorValue(value?: number, zeroFill = false, len = 2, valueType = '') {
  // undefined时返回0
  if (isNil(value) || Number(value) === 0) {
    return '0';
  }
  if (valueType === 'int' && value < 1_0000) {
    return toTenPercentile(value);
  }
  let number = `${floor(value, len)}`;
  if (zeroFill && len > 0) {
    const fixIndex = number.indexOf('.');
    if (fixIndex === -1) {
      number += '.';
    }
    const decimalsIndex = fixIndex === -1 ? 0 : number.length - number.indexOf('.') - 1;
    let i = len - decimalsIndex;
    while (i--) {
      number += '0';
    }
  }
  return number;
}
// 四舍五入，保留传入位数小数
export function moneyFormat(value: number, len = 2, onlyTenPercentile = false, divide = 100) {
  if (isNil(value)) {
    return '-';
  }
  // 将钱从分转为元
  const money = value / divide;
  if (onlyTenPercentile) {
    return toTenPercentile(money);
  }
  if (Math.abs(money) >= 1e8) {
    return `${toFixFloorValue(money / 1e8, true, len)}亿`;
  }
  if (Math.abs(money) >= 1e4) {
    return `${toFixFloorValue(money / 1e4, true, len)}万`;
  }
  return `${toFixFloorValue(money, true, len)}`;
}

export function transBigNum(value: number, len = 2, onlyTenPercentile = false) {
  const val = value;
  if (isNil(val)) {
    return '-';
  }
  if (onlyTenPercentile) {
    return toTenPercentile(val);
  }
  if (Math.abs(val) >= 1e8) {
    return `${toFixFloorValue(val / 1e8, true, len)}亿`;
  }
  if (Math.abs(val) >= 1e4) {
    return `${toFixFloorValue(val / 1e4, true, len)}万`;
  }
  return `${toFixFloorValue(val, true, len, 'int')}`;
}


// 判断非0的假值
export const checkIsFalsyExcludeZero = (value: any): boolean => {
  return !value && (value || value !== 0);
};

// 模糊展示数字
export const resolveNumToVirtualStr = (num: number) => {
  if (num <= 100) {
    return num.toString();
  }
  if (num > 1000) {
    return '1000+';
  }
  const virtualNum = Math.floor(num / 100) * 100;
  return `${virtualNum}+`;
};

// 千分位格式化数字
export const formatThousand = (num: number | string, decimal = 2) => {
  return (
    Number(num)
      .toFixed(decimal)
      // eslint-disable-next-line @ecom/security-unsafe-regex
      .replace(/\B(?=(\d{3})+(?!\d))/g, ',')
  );
};

// 格式化百分比
export function formatPercentage(ratio: number, digits = 1) {
  return `${(ratio * 100).toFixed(digits)}%`;
}

// 数字模糊展示 xx+
export const rangeNumberUnit = (num: number) => {
  let virtualNum = '';
  if (num <= 100) {
    return num.toString();
  } else if (num < 1000) {
    virtualNum = `${Math.floor(num / 100) * 100}+`;
  } else if (num < 10000) {
    virtualNum = `${Math.floor(num / 1000) * 1000}+`;
  } else if (num < 100000) {
    virtualNum = `${Math.floor(num / 10000)}w+`;
  } else {
    virtualNum = '10W+';
  }
  return virtualNum;
};

// 数字转万/亿, 保留两位小数
export const formatBigNum = (num?: number | string, keepDigit = 2, noformatDigit = 0) => {
  num = Number(num);
  if (num < 1e4) return num.toFixed(noformatDigit);
  if (num > 9999 && num < 1e8) {
    return `${(Number(num) / 1e4).toFixed(keepDigit)}万`;
  }
  if (num >= 1e8) {
    return `${(num / 1e8).toFixed(keepDigit)}亿`;
  }
  return '';
};

// 数字转万/亿, 保留两位小数
export const formatBigNumArray = (
  num?: number | string,
  keepDigit = 2,
  noformatDigit = 0,
  needFormatThousand = false,
  zeroValue = '0'
): [string, string, string] => {
  num = Number(num);
  if (num === 0) return [zeroValue, '', zeroValue];
  if (num < 1e4) {
    if (needFormatThousand) {
      return [
        formatThousand(num.toFixed(noformatDigit), noformatDigit),
        '',
        formatThousand(num.toFixed(noformatDigit), noformatDigit),
      ];
    }
    return [num.toFixed(noformatDigit), '', num.toFixed(noformatDigit)];
  }
  if (num > 9999 && num < 1e8) {
    if (needFormatThousand) {
      return [
        `${formatThousand((Number(num) / 1e4).toFixed(keepDigit), keepDigit)}`,
        '万',
        `${formatThousand((Number(num) / 1e4).toFixed(keepDigit), keepDigit)}万`,
      ];
    }
    return [`${(Number(num) / 1e4).toFixed(keepDigit)}`, '万', `${(Number(num) / 1e4).toFixed(keepDigit)}万`];
  }
  if (num >= 1e8) {
    if (needFormatThousand) {
      return [
        `${formatThousand((Number(num) / 1e8).toFixed(keepDigit), keepDigit)}`,
        '亿',
        `${formatThousand((Number(num) / 1e8).toFixed(keepDigit), keepDigit)}亿`,
      ];
    }
    return [`${(Number(num) / 1e8).toFixed(keepDigit)}`, '亿', `${(Number(num) / 1e8).toFixed(keepDigit)}亿`];
  }
  return ['', '', ''];
};

// 拆分拼接单位的字符串数量，如12.5万
export const divideNumWithUnit = (num: number | string) => {
  num = num.toString();
  const unit = num.replace(/[\d\.]+/, '');
  const value = unit ? num.slice(0, -1) : num;
  return [value, unit];
};

// 转化数字，保留两位小数，但是不拼接单位
export const formatBigNumWithoutUnit = (inputNum?: number | string | null, keepDigit = 1) => {
  let num = null;

  if ((inputNum && typeof inputNum === 'string') || typeof inputNum === 'number') {
    num = Number(inputNum);
  } else {
    return '-';
  }

  if (isNaN(num as number)) {
    return '-';
  }

  if (num < 1e4) return num;
  if (num > 9999 && num < 1e8) {
    return `${(Number(num) / 1e4).toFixed(keepDigit)}`;
  }
  if (num >= 1e8) {
    return `${(num / 1e8).toFixed(keepDigit)}`;
  }
  return '';
};

// 获取大数字的单位
export const formatUnit = (inputNum?: number | string | null) => {
  let num = null;

  if ((inputNum && typeof inputNum === 'string') || typeof inputNum === 'number') {
    num = Number(inputNum);
  } else {
    return '';
  }

  if (isNaN(num as number)) {
    return '';
  }

  if (num < 1e4) return '';
  if (num > 9999 && num < 1e8) {
    return '万';
  }
  if (num >= 1e8) {
    return '亿';
  }
  return '';
};

// rate组件 十分位大于8进一
export const formatScore = (score: number) => {
  const strScore = String(score)?.split('.');
  if (strScore.length < 2) return score;
  const decimal = strScore[1];
  if (parseInt(decimal[0]) >= 8) {
    return parseInt(String(score)) + 1;
  }
  return score;
};

// 100 以下数字转汉字
export const transNumToChinese = (num: number) => {
  const baseMap = ['', '一', '二', '三', '四', '五', '六', '七', '八', '九', '十'];
  if (num <= 10) return baseMap[num];
  return num
    .toString()
    .split('')
    .map(item => baseMap[item])
    .join('十');
};
