import React, { useMemo, useState } from 'react';
import { Checkbox, Tabs } from 'antd';

import s from './index.m.scss';

const CheckboxGroup = Checkbox.Group;

const options = ['left', 'right'];

export type PositionType = 'left' | 'right';

const items = new Array(3).fill(null).map((_, i) => {
  const id = String(i + 1);
  return {
    label: `Tab ${id}`,
    key: id,
    children: `Content of tab ${id}`,
  };
});
type ReactElementBtn = React.ReactNode | React.ReactNode[];
interface IOperation {
  operationsSlot: Record<PositionType, ReactElementBtn>
}

export function Operation<OPT>(props: OPT extends IOperation ? OPT : IOperation) {

  const { operationsSlot } = props;
  const [position, setPosition] = useState<PositionType[]>(['left', 'right']);

  const slot = useMemo(() => {
    if (position.length === 0) return null;

    return position.reduce(
      (acc, direction) => ({ ...acc, [direction]: operationsSlot[direction] }),
      {},
    );
  }, [position]);

  return (
    <div className={s.wrap}>
      <CheckboxGroup
        options={options}
        value={position}
        onChange={(value) => {
          setPosition(value as PositionType[]);
        }}
      />
      <br />
      <Tabs
        tabBarExtraContent={slot}
        items={items}
        className={s.tabs}
      />
    </div>
  );
};
