import React from 'react';

import { Col, Row, Tag, Tooltip, Image } from 'antd';
import EllipsisTags from '../ellipsis-tags';
import classNames from 'classnames';

import { CopyId } from '../copy';

import s from './ItemInfo.module.scss';
const highlightLabelTag = {
  1: 'react',
  2: 'vue',
  3: 'antd',
}
interface IImgInfo {
  url: string;
  size?: number;
  width?: number;
  height?: number;
  borderRadius?: number;
  defaultUrl?: string;
  marginRight: number;
}
interface IItemInfo {
  label?: string;
  isClosePromotion?: boolean;
  lineClamp?: number;
  imgInfo?: IImgInfo;
  id?: string;
  maxWidth?: number;
  tags?: Array<number>;
  highlight_label?: number[];
  navigateTo?: () => void;
}
export function ItemInfo(props: IItemInfo) {
  const { label, isClosePromotion, lineClamp = 1, imgInfo, id, highlight_label, navigateTo } = props;
  const { size, width, height, borderRadius, url = '' } = imgInfo ?? {};
  const [_width, _height] = size ? [size, size] : [36, 36];
  const isNavigable = 'function' === typeof navigateTo;
  const onNavigateToDetail = (): any => {
    isNavigable && navigateTo();
  };
  const getTags = () => {
    const tags = [];
    if (id) {
      tags.push(
        <Tooltip title={<CopyId message="复制成功" id={id} />}>
          <span className={s.content_id} />
        </Tooltip>
      );
    }
    return tags;
  };

  return (
    <>
      <Row className={s.info}>
        {imgInfo ? (
          <Col className={s.goodsPhoto} style={{ marginRight: imgInfo?.marginRight }}>
            <div>
              <Image
                width={width ?? _width}
                height={height ?? _height}
                src={url}
              />
            </div>
          </Col>
        ) : null}
        <Col className={s.content} style={!imgInfo ? { maxWidth: '100%' } : {}}>
          <div className={isNavigable ? classNames(s.hoverPoint, s.content_text) : s.content_text}>
            <EllipsisTags
              // content={label}
              content={id}
              extendContents={getTags()}
              line={lineClamp}
              extendPlacement={'topRight'}
              expandTop={3}
              contentClick={onNavigateToDetail}
            />
          </div>
          {highlight_label?.length ? (
            <Row gutter={[4, 0]}>
              {highlight_label.map(l => (
                <Col key={l}>
                  <Tag
                    color="#F5F5FF"
                    style={{
                      color: '#6557FA',
                    }}
                  >
                    {highlightLabelTag[l]}
                  </Tag>
                </Col>
              ))}
            </Row>
          ) : null}
        </Col>
      </Row>
      {isClosePromotion ? (
        <Row>
          <div className={s.isClosePromotion}>已下架</div>
        </Row>
      ) : (
        <></>
      )}
    </>
  );
}
