import React from 'react';
import { CouponHover } from '@apis/_ferry/api-lib/ecom.kol.selection_pc_api/promotion';
import classNames from 'classnames';
import styles from '../../index.module.scss';

interface Iprops {
  coupon_hover: CouponHover[];
  border_color?: string;
}

export default (props: Iprops) => {
  const { coupon_hover, border_color } = props;

  return (
    <>
      {coupon_hover?.length
        ? coupon_hover.map((item: CouponHover, index: number) => {
            const { text_left = {}, text_right = {} } = item || {};
            const { text: leftText, color, bg_color } = text_left;
            const { text: rightText, color: rightColor, bg_color: rightBgColor } = text_right;
            const borderStyle = { border: `1px solid ${border_color}` };
            const lineStyle = { borderLeft: `0.5px dashed ${border_color}` };

            return (
              <div key={`${index}-${leftText}`} className={styles['label-configuration-hover']}>
                <span className={styles['label-configuration-hover_text']} style={borderStyle}>
                  <span
                    className={classNames(
                      styles['label-configuration-hover_text_main'],
                      rightText && styles['label-configuration-hover_text_pd3']
                    )}
                    style={{ color: color, backgroundColor: bg_color }}
                  >
                    {leftText}
                  </span>
                  {rightText ? (
                    <span
                      className={styles['label-configuration-hover_text_right_text']}
                      style={{ color: rightColor, backgroundColor: rightBgColor, ...lineStyle }}
                    >
                      {rightText}
                    </span>
                  ) : null}
                </span>
              </div>
            );
          })
        : null}
    </>
  );
};
