import { Key } from 'react';
export interface GetSelectionProductItem {
  /** 商品id */
  product_id: string;
  /** 商品名称 */
  product_name: string;
  /** 商品图片 */
  product_img: string;
  /** 推广id */
  promotion_id: string;
  /** 商品售价（单位分） */
  product_price: number;
  /** 商家id */
  shop_id: string;
  /** 商家名称 */
  shop_name: string;
  /** 商家联系电话 */
  shop_phone: string;
  /** 原始机构百应id（投放商品该字段不为空） */
  origin_institution_buyin_id: string;
  /** 原始机构名称 */
  origin_institution_name: string;
  /** 活动id（如果是托管商品则活动id为0） */
  origin_activity_id: string;
  /** 活动名称 */
  origin_activity_name: string;
  /** 佣金率 */
  commission_rate: string;
  /** 服务费率 */
  service_rate: string;
  /** 近30日总销量 */
  month_sales: number;
  /** 近30日联盟销量 */
  alliance_month_sales: number;
  /** 总销量 */
  total_sales: number;
  /** 联盟销量 */
  alliance_total_sales: number;
  /** 近30日浏览量 */
  views: number;
  /** 推广达人数 */
  promoters_count: number;
  /** 带货类型，第一个值表示直播占比，第二个值表示视频占比 */
  channel_type_percent: Array<number>;
  /** 合作商品类型，1投放商品，2线索商品 */
  product_type: number;
  /** 关联直播数 */
  bind_live_count: number;
  /** 关联视频数 */
  bind_video_count: number;
  /** 月下单率，如98.76 */
  month_order_rate: string;
  /** 商品等级，G0-G4 */
  product_level: string;
  /** 店铺体验分 */
  shop_score: string;
  /** 商品好评率，如98.76 */
  product_favorable_rate: string;
  /** 商品库存 */
  product_stock: number;
  /** 线索商品托管团长的百应id */
  trusteeship_institution_buyin_id: string;
  /** 收藏分组id */
  group_id: string;
  /** 是否已收藏 */
  in_collection: boolean;
  /** 是否可加入活动 */
  can_add: boolean;
  /** 不可加入活动原因 */
  cannot_add_reason: string;
  /** 原始机构id */
  origin_institution_id?: string;
  /** 线索商品托管团长的id */
  trusteeship_institution_id?: string;
  req_mode?: string;
  /** 合作亮点标签(1：可聊投流 2：可聊独家 3：可聊托管) */
  highlight_label?: Array<number>;
}

interface ISelectGoods extends GetSelectionProductItem {
  p_id: string;
}

export interface IDataSource extends ISelectGoods {
  key: Key;
}

export enum PoolColumnKeyMap {
  product_price = 'product_price',
  commission_rate = 'commission_rate',
  service_rate = 'service_rate',
  month_sales = 'month_sales',
  views = 'views',
  promoters_count = 'promoters_count',
  alliance_month_sales = 'alliance_month_sales',
  total_sales = 'total_sales',
  alliance_total_sales = 'alliance_total_sales',
}