import React, { CSSProperties, ReactNode, useCallback, useEffect, useMemo, useRef } from 'react';
import './index.scss';
import { debounce } from 'lodash';

type TLastEleRef = React.MutableRefObject<HTMLDivElement>;
interface Props {
  hasMore: boolean;
  isFetching: boolean;
  isFetchFailed: boolean;
  errorText?: string | ReactNode;
  noMoreText?: string | ReactNode;
  fetchingText?: string | ReactNode;
  onRetry?: Function;
  onActive: () => void;
  styles?: CSSProperties;
  fetchBuffer?: number;
  options?: IntersectionObserverInit
}

export const LoadMore = React.memo((props: Props) => {
  const {
    hasMore,
    isFetching,
    isFetchFailed,
    onRetry,
    onActive,
    errorText = '加载失败，点击重试',
    noMoreText = '没有更多内容啦',
    fetchingText = '正在加载',
    styles = {},
    fetchBuffer = 0,
    options
  } = props;
  const loadMoreRef = useRef<HTMLDivElement>(null);
  const debounceActive = useCallback(debounce(onActive, 500, { leading: true }), [
    onActive,
    hasMore,
    isFetching,
    isFetchFailed,
  ]);

  useEffect(() => {
    const observer = new IntersectionObserver(entries => {
      entries.forEach(entry => {
        const { isIntersecting } = entry;
        // 视窗内可见就加载
        if (isIntersecting) {
          // onActive();
          debounceActive();
        }
      });
    }, options);
    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current as HTMLDivElement);
    }

    return () => {
      observer.disconnect();
    };
  }, [debounceActive, hasMore, onActive]);

  const contentText = useMemo(() => {
    if (isFetching) {
      return fetchingText;
    }
    if (isFetchFailed) {
      return errorText;
    }
    return '';
  }, [isFetching, isFetchFailed, hasMore]);

  const handleRetry = useCallback(() => {
    if (isFetchFailed && !isFetching && hasMore && typeof onRetry === 'function') {
      onRetry();
    }
  }, [isFetchFailed, isFetching, hasMore, onRetry]);
  return (
    <div ref={loadMoreRef} className='loader_wrap' style={{ transform: `translate3d(0, ${-fetchBuffer}px, 0)` }} >
      {hasMore ? (
        <div className="loadmore hasMore" style={{ ...styles }} onClick={handleRetry}>
          {isFetching && <span className="loadmore__spinner" />}
          <span className="loadmore__text">{contentText}</span>
        </div>
      ) : (
        <div className="loadmore" style={{ ...styles }}>
          <span className="loadmore__text one-third-font-color">{noMoreText}</span>
        </div>
      )}
    </div>
  );
});
