import { useCallback, useContext } from 'react';

import { ConfigContext } from 'antd/es/config-provider';
import classnames from 'classnames';

const useBaseClassnames = (prefix: string) => {
  const { getPrefixCls } = useContext(ConfigContext);
  const auxoPrefix = getPrefixCls();
  const cn = useCallback(
    (...args) => {
      return classnames(
        ...args.map(el => {
          return typeof el === 'string' ? `${auxoPrefix}-dorami-${prefix}-${el}` : el;
        })
      );
    },
    [prefix, auxoPrefix]
  );
  return cn as typeof classnames;
};

export const useClassnames = () => {
  // 组件前缀，与less保持一致
  // return useBaseClassnames('setcolumns');
  // 暂时不添加前缀
  return classnames
};

export const usePrefix = () => {
  const { getPrefixCls } = useContext(ConfigContext);
  const auxoPrefix = getPrefixCls();
  // 组件前缀，与less保持一致
  return `${auxoPrefix}-dorami-setcolumns`;
};
