/* eslint-disable @ecom/security-unsafe-redirect-target */
/**
 * goods card
 */
import React, { useMemo, useEffect, useRef } from 'react';
import { stringify } from 'qs';
import { Button, Image, Tooltip } from 'antd';

import Icon, { RightOutlined } from '@ant-design/icons';
import { priceFormat } from '@global/utils/number/price';
import { getQuery } from '@global/utils/routers/query';
// import useAddWindow from '@hooks/useAddWindow';
import cx from 'classnames';
// import { useContextStore } from '@ecom/lite-store';
import Tip from '../tip';
import styles from './index.m.scss';
import { renderPicProductTags, renderTextProductTags, HasLabelAction } from '../label-configuration';
import CouponTag from '../coupon-tag';
import { getFilterLabelLogParams } from './util';
import { IDataSourceSelect } from 'pages/daren/select-goods/model/apis';

interface IProps {
  goodsInfo: IDataSourceSelect;
  showDis?: boolean;
  isFixPromotion?: boolean;
  // 显示模式 default默认/rank榜单/luckybag福袋商品
  mode?: 'default' | 'rank' | 'luckybag';
  className?: string;
  logParams?: Record<string, string | undefined | number | boolean>;
  toChatPage?: (shopId?: number) => void;
  extraQuery?: Record<string, any>;
  handleDistribution: () => void | undefined;
}

export const formatSalesFunc = (sales?: number) => {
  if (sales === undefined) return '';
  return sales > 10000
    ? `${Number.isInteger(sales / 10000) ? sales / 10000 : Math.round((sales / 10000) * 100) / 100}万`
    : sales;
};

export const GoodsCard = (props: IProps) => {
  const {
    goodsInfo,
    showDis = false,
    mode = 'default',
    isFixPromotion = false,
    className,
    logParams: logParamsFromProps = {},
    extraQuery = {},
    handleDistribution,
  } = props;
  const queryObj = getQuery() || {};
  const cardDomRef = useRef<HTMLDivElement>(null);
  const {
    key,
    cover,
    title,
    price,
    month_sales,
    shop_name,
    exp_score,
    cos_fee,
    in_shop,
    product_id,
    promotion_id,
    privilege_info,
    tiered_cos,
    is_product_distribution,
    cos_ratio,
  } = goodsInfo;


  const isRankMode = useMemo(() => mode === 'rank', [mode]);
  const isLuckybagMode = useMemo(() => mode === 'luckybag', [mode]);
  const isPromotionMode = useMemo(() => mode === 'default', [mode]);

  const formatMonthSales = useMemo(() => {
    return formatSalesFunc(month_sales);
  }, [month_sales]);

  const formatCosRatio = useMemo(() => {
    return `${cos_ratio}%`;
  }, [cos_ratio]);

  const formatSales = useMemo(() => {
    return '23212';
  }, []);

  const rankSalesDescText = useMemo(() => {
    return '近30天';
  }, []);

  useEffect(() => {
    // 判断当前卡片是否曝过光
    const hasNotExposed = cardDomRef.current?.hasAttribute('data-observe');
    let intersectionObserver: IntersectionObserver | null = null;

    if (product_id && hasNotExposed) {
      // 曝光埋点：商品出现在可视区域时再上报埋点
      intersectionObserver = new IntersectionObserver(
        ([node]) => {
          // 递归每一个本次被监听对象，如果按照曝光条件出现在可视区，则调用callback函数，并且取消监听
          if (node.isIntersecting) {
            node?.target.removeAttribute('data-observe');

            let enterFromData = {};
            try {
              enterFromData = queryObj['enter_from'] ? JSON.parse(queryObj['enter_from']) : {};
            } catch (e) { }
            const filterParams = getFilterLabelLogParams(goodsInfo, extraQuery);
            intersectionObserver && intersectionObserver.unobserve(node.target);
          }
        },
        {
          root: null,
          rootMargin: '0px',
          threshold: 0.1,
        }
      );
      cardDomRef.current && intersectionObserver?.observe(cardDomRef.current);
      return () => {
        intersectionObserver && intersectionObserver?.disconnect();
      };
    }
  }, [
    product_id,
    privilege_info?.privilege_id,
  ]);

  // 阶梯佣金的各阶梯佣金及佣金率
  const [tieredCos0] = tiered_cos?.tiered_cos_list || [];
  const goGoodsDecision = () => {
    console.log('goGoodsDecision')
  }

  const renderPrice = () => {
    if (isLuckybagMode) {
      return (
        <span>
          {goodsInfo?.can_supply ? '采购价' : '售价'}
          <span className={styles.commission}>
            {goodsInfo?.can_supply && price >= 1e7 ? (
              <span className={cx(styles.fontWeight800, styles.priceFormat)}>
                <span className={styles.priceFormat__fixed}>10</span>
                <span className={styles.priceFormat__text}>万以上</span>
              </span>
            ) : (
              <span className={cx(styles.fontWeight800, styles.priceFormat)}>
                <span className={styles.priceFormat__amountUnit}>¥</span>
                <span className={styles.priceFormat__large}>{priceFormat(price || 0).split('.')[0]}</span>
                <span className={styles.priceFormat__amountUnit}>.{priceFormat(price || 0).split('.')[1]}</span>
              </span>
            )}
          </span>
        </span>
      );
    }

    return (
      <Tip tiered_cos={tiered_cos}>
        <span>
          赚
          <span className={styles.commission}>
            {tieredCos0 ? (
              <>
                <span className={cx(styles.fontWeight800, styles.priceFormat)}>
                  <span className={styles.priceFormat__amountUnit}>¥</span>
                  <span className={styles.priceFormat__large}>
                    {priceFormat(tieredCos0?.cos_fee || 0).split('.')[0]}
                  </span>
                  <span className={styles.priceFormat__amountUnit}>
                    .{priceFormat(tieredCos0?.cos_fee || 0).split('.')[1]}
                  </span>
                </span>
              </>
            ) : (
              <>
                <span className={cx(styles.fontWeight800, styles.priceFormat)}>
                  <span className={styles.priceFormat__amountUnit}>¥</span>
                  <span className={styles.priceFormat__large}>{priceFormat(cos_fee || 0).split('.')[0]}</span>
                  <span className={styles.priceFormat__amountUnit}>.{priceFormat(cos_fee || 0).split('.')[1]}</span>
                </span>
              </>
            )}
          </span>
        </span>
      </Tip>
    );
  };

  const renderBtn = () => {
    return (
      <Button type="primary" size="small" onClick={handleDistribution}>
        立即推广
      </Button>
    );
  };

  const [old_price] = [12];

  return (
    <>
      <div className={cx(styles.pickGoodsCard, className)} ref={cardDomRef} data-observe>
        {isRankMode && (
          <div
            className={cx(styles.rankTag, styles.newRankTag, {
              [styles.rankTagTop1]: true,
              [styles.rankTagTop2]: false,
              [styles.rankTagTop3]: false,
            })}
          >
            {'rank'}
          </div>
        )}
        <div
          className={styles.coverWrapper}
          onClick={() => {
            goGoodsDecision();
          }}
        >
          <Image className={styles.cover} src={cover?.url_list?.length ? cover.url_list[0] : ''} />
          <p className={styles.coverMask} />
          <div className={styles.keyTag}>{key}</div>
        </div>
        <div className={styles.goodsInfo}>
          <div
            className={isLuckybagMode ? styles.goodsInfo__titleTwoLine : styles.goodsInfo__title}
            onClick={() => {
              goGoodsDecision();
            }}
          >
            {/* 无忧先暂时福袋特殊处理 */}
            <div className={styles.goodsInfo__title__tags}>
              {renderPicProductTags({
                ...goodsInfo,
              } as IDataSourceSelect)}
            </div>
            {isRankMode && renderPicProductTags(goodsInfo as IDataSourceSelect)}
            <span className={styles.goodsInfo__title__text}>{title}</span>
          </div>
          <div className={styles.goodsInfo__luckycoupon}>
            <CouponTag name="券" content="可用优惠券" />
          </div>
          <div className={styles.goodsInfo__sale}>
            {!isLuckybagMode && (
              <span className={styles.priceFormat}>
                <span className={styles.priceFormat__amountUnit}>¥</span>
                <span className={styles.priceFormat__large}>{priceFormat(price || 0).split('.')[0]}</span>
                <span className={styles.priceFormat__amountUnit}>.{priceFormat(price || 0).split('.')[1]}</span>
              </span>
            )}
            {
              // eslint-disable-next-line no-nested-ternary
              isRankMode ? (
                <span className={styles.goodsInfo__sales}>推广费率 {formatCosRatio}</span>
              ) : isLuckybagMode ? (
                <>
                  {Boolean(old_price > price) && (
                    <span className={styles['goodsInfo__old-price']}>
                      售价 ¥{priceFormat(old_price || 0).split('.')[0]}.{priceFormat(12 || 0).split('.')[1]}
                    </span>
                  )}
                  <span className={styles.goodsInfo__sales}>月销 {formatMonthSales}</span>
                </>
              ) : (
                <span className={styles.goodsInfo__sales}>月售 {formatMonthSales}</span>
              )
            }
          </div>
          {isRankMode && (
            <div className={styles.goodsInfo__rankSales}>
              {rankSalesDescText}销量 {formatSales}
            </div>
          )}
          {isPromotionMode && (
            <div className={styles.goodsInfo__activity}>
              {/* 无忧先暂时福袋特殊处理 */}
              {renderTextProductTags(goodsInfo, HasLabelAction.HOVER)}
            </div>
          )}
          <div className={styles.shopInfo}>
            {
              <>
                <span className={styles.shopName}>{shop_name}</span>
                {exp_score ? <span>{exp_score}分</span> : <span style={{ whiteSpace: 'nowrap' }}>暂无评分</span>}
              </>
            }
          </div>
          <div className={styles.footer}>
            {renderPrice()}
            {renderBtn()}
          </div>
          {isPromotionMode && (
            <div className={styles.extraInfo}>
              <div
                className={styles.rankInfo}
                onClick={e => {
                  e.stopPropagation();
                  const { category_id, tab_id, type, billboard_type } = {};
                  // 专属选品广场
                  if (/dashboard\/exclusive-selection-square/.test(window.location.pathname)) {
                    window.open(
                      `/dashboard/exclusive-selection-square/rank?${stringify({
                        rank_info: { category_id, tab_id, type, billboard_type },
                      })}`
                    );
                    return;
                  }
                  window.open(
                    `/dashboard/douke/merch-picking-hall${type === 2 ? '/rank?' : '?default_tab=4&'}${stringify({
                      rank_info: { category_id, tab_id, type, billboard_type },
                    })}&source={2323}`
                  );
                }}
              >
                {'rank_text'}
                {/* <Icon component={<RightOutlined />} /> */}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

