import { checkIsFalsyExcludeZero } from './number';

/**
 * 格式化 金额/数量  千分位， xxx万, xxx亿
 * @param {number} num 金额/数量 数字
 * @return {string} 格式化后字符串，千分位，,xxx万, xxx亿
 */
export function numFormatSuffix(num?: number): string {
  if (num === undefined) {
    return '-';
  }
  if (num > 9999 && num < 1e8) {
    return `${Math.floor((num / 1e4) * 10) / 10}万`;
  }
  if (num >= 1e8) {
    return `${Math.floor((num / 1e8) * 10) / 10}亿`;
  }
  if (num < 1e4) {
    return num.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
  }
  return num.toString();
}
/**
 * 格式化 金额/数量 为千分位
 * @param {number} num 金额/数量 数字
 * @return {string} 格式化千分位的字符串
 */
export function numFormat(num?: number): string {
  if (num === undefined) {
    return '-';
  }
  if (num.toString().indexOf('.') !== -1) {
    return num.toLocaleString();
  } else {
    return num.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
  }
}

/**
 * 格式化 金额/数量  xxx万, xxx亿
 * @param {number} num 金额/数量 数字
 * @return {string} 格式化后字符串，xxx万, xxx亿
 */
export function numSuffix(num: number): string {
  if (num > 9999 && num < 1e8) {
    return `${Math.floor((num / 1e4) * 10) / 10}万`;
  }
  if (num >= 1e8) {
    return `${Math.floor((num / 1e8) * 10) / 10}亿`;
  }
  return num.toString();
}

// 由于4.0和lint的冲突tuple声明从元组改成字符数组
export function numSuffixWithSign(num: number, rule?: 'floor' | 'round' | 'ceil'): string[] {
  let resultNum = 0;
  let resultSign = '';
  const carryRule = rule || 'floor';
  if (num <= 9999) {
    resultNum = num;
  }
  if (num > 9999 && num < 1e8) {
    resultNum = Math[carryRule]((num / 1e4) * 100) / 100;
    resultSign = '万';
  }
  if (num >= 1e8) {
    resultNum = Math[carryRule]((num / 1e8) * 100) / 100;
    resultSign = '亿';
  }
  const resultNumStr = resultNum.toString().replace(/\d+/, s => s.replace(/(\d)(?=(\d{3})+$)/g, '$1,'));
  return [resultNumStr, resultSign];
}

/**
 * 格式化 金额/数量  xxx万, xxx亿，默认留存两位小数
 * @param {number} num 金额/数量 数字
 * @return {string} 格式化后字符串，xxx万, xxx亿
 */
export function formatNumber(num: number): string {
  if (num > 9999 && num < 1e8) {
    return `${(num / 1e4).toFixed(2)}万`;
  }
  if (num >= 1e8) {
    return `${(num / 1e8).toFixed(2)}亿`;
  }
  return num.toFixed(2);
}

/**
 * 格式化 金额/数量  xxx万, xxx亿，默认留存两位小数
 * @param {number} num 金额/数量  @param {number} decimal 保留小数
 * 小于一万返回千分位
 * @return {string[]} 格式化数组，第一位是数字，第二位是单位，如果没有则为空
 */
export function formatNumberWithArray(num?: number, decimal = 2): string[] {
  // 增加undefined的兼容, 返回-
  if (num === undefined) {
    return ['-'];
  }
  let res = [];
  // 小于一万直接返回
  if (num <= 9999) {
    return [numFormat(num).toString()];
  }
  if (num > 9999 && num < 1e8) {
    res = [(num / 1e4).toFixed(decimal).toString(), '万'];
    return res;
  }
  if (num >= 1e8) {
    res = [(num / 1e8).toFixed(decimal).toString(), '亿'];
    return res;
  }
  return [num.toFixed(decimal).toString()];
}

/**
 * 格式化人民币单位分到元
 * @param {number} num 金额/数量 数字
 * @return {string} 格式化后字符串
 */
export function formatGMV(num: number): string {
  return (num / 100).toFixed(2);
}

/**
 * 格式化数字
 * 1. 小于万：具体数字
 * 2. 大于万：以万为单位；精确到小数点后两位，如:1万、1.2万、2.33万
 * 3. 大于亿：以亿为单位；精确到小数点后两位，如:1亿、1.2亿、2.33亿
 * 4. 删除非有效位0
 * 5. 数字为0时，数字显示0；
 * @param num
 * @returns string
 */
export function enhancedNumberSuffix(num: number): string {
  if (num === undefined) return '0';
  if (num > 9999 && num < 1e8) {
    return `${Number((num / 1e4).toFixed(2))}万`;
  } else if (num >= 1e8) {
    return `${Number((num / 1e8).toFixed(2))}亿`;
  }
  return String(Number(num.toFixed(2)));
}

/**
 * 为金额单位末尾补零
 * 如: ￥1.1 => ￥1.10
 * 如: ￥1 => ￥1.00
 * @param value
 * @returns {string}
 */
export function addZeroToAmount(value: string): string {
  if (value.includes('.')) {
    if (value.split('.')[1].length === 1) {
      return `${value}0`;
    }
    return value;
  } else {
    return `${value}.00`;
  }
}

/**
 * 转换人民币金额单位 元 为 分
 * @param {number} num 金额数字
 * @return {number} 转换为分的金额
 */
export function convertAmountDigitToCent(num: number): number {
  return num * 100;
}

/**
 * 转换人民币金额单位 分 为 元
 * @param {number} num 金额数字
 * @return {number} 转换为元的金额
 */
export function convertAmountDigitToYuan(num: number): number {
  return num / 100;
}

/**
 * 转换人民币金额单位 分 为 元，并格式化千分位
 * @param {number} balance 金额数字
 * @return {string} 转换为元的千分位金额
 */
export const formatBalance = (balance: number): string => {
  return formatBalanceWithNotDecimal(convertAmountDigitToYuan(balance).toFixed(2));
};

/**
 * 转换人民币金额单位 分 为 元，格式化千分位，并加上人民币单位
 * @param {number} balance 金额数字
 * @return {string} 转换为元的千分位金额
 */
export const formatBalanceWithRMBUnit = (balance: number): string => {
  return `￥${formatBalance(balance)}`;
};

/**
 * 转换金额为千分位，排除小数点的影响
 * @param {number} balance 金额数字
 * @return {string} 转换为元的千分位金额
 */
export function formatBalanceWithNotDecimal(balance: number | string): string {
  const [intValue, floatValue] = balance.toString().split('.');
  return numFormat(Number(intValue)) + (floatValue ? `.${floatValue}` : '');
}

/**
 * 格式化用户金额输入，去除非数字非小数点的字符，同时只保留一个小数点
 * @param {number | string} balance 金额数字
 * @return {string} 格式化之后字符串
 */
export function formatBalanceInputValue(balance: number | string = '') {
  return balance
    .toString()
    .replace(/[^\d\\.]/g, '')
    .replace(/(\d*)(\.\d{0,2})?(.*)/g, (_: string, $1 = '', $2 = '') => $1 + $2);
}

// 不足1W，按实际显示。否则以W单位，整数显示
export const formatTenThousand = (num: number): string => {
  if (num < 10000) {
    return String(num);
  }
  return `${String(num).substring(0, String(num).length - 4)} 万`;
};

export const formatTenThousandWithSign = (num: number): string[] => {
  if (num < 10000) {
    return [String(num), ''];
  }
  return [String(num).substring(0, String(num).length - 4), '万'];
};

//  价格格式化为**,***,***.**  price传 1999900 显示为 19,999.00
export const priceFormat = (price: number): string => {
  return numFormat(Number((price / 100).toFixed(2).split('.')[0])).concat('.', (price / 100).toFixed(2).split('.')[1]);
};

//  价格格式化为**,***,***.**  price传 1999900 显示为 19,999.00，为falsy时给占位字符
export const priceFormatWithPlaceholder = (price?: number): string => {
  if (checkIsFalsyExcludeZero(price)) return '-';
  return numFormat(Number((price / 100).toFixed(2).split('.')[0])).concat('.', (price / 100).toFixed(2).split('.')[1]);
};

/**
 * 宽度不足时的数字格式化方案
 * 不足万时，完整显示，千分位分隔；
 * 超过万时，1-99万，保留小数点后一位，为0则不显示；
 * 超过100万时，仅保留整数；
 * 亿以上的不做特殊处理。
 * @param num
 * @returns {string[]} 格式化数组，第一位是数字，第二位是单位，如果没有则为空
 */
export const formatNumberWithLimitWidth = (num: number) => {
  let res = [];
  if (num <= 9999) {
    return [numFormat(num).toString()];
  }
  if (num > 9999 && num < 1e6) {
    res = [parseFloat((num / 1e4).toFixed(1)).toString(), '万'];
    return res;
  }
  if (num > 1e6 - 1 && num < 1e8) {
    res = [parseInt((num / 1e4).toFixed()).toString(), '万'];
    return res;
  }
  if (num >= 1e8) {
    return numSuffixWithSign(num);
  }
  return [num.toFixed().toString()];
};

export interface FormatNumberWithStringOptions {
  handleDecimal?: boolean; // 是否处理小数部分
  decimal?: number; // 小数点保留位数
  removeDecimalZero?: boolean; // 去除小数点后多余的零
  shortFormat?: boolean; // 是否超出万亿缩写 xxx万 xxx亿
  thousands?: boolean; // 万以下千以上显示千分位
  returnArray?: boolean; // 单位和数字是否数组形式返回
}
export type ReturnValue = [string, string] | string;
export const formatNumberWithString = (num: number, options?: FormatNumberWithStringOptions): ReturnValue => {
  const realNum = Number(num);

  if (isNaN(realNum)) return String(num);

  const {
    handleDecimal = true,
    decimal = 2,
    removeDecimalZero = true,
    shortFormat = true,
    thousands = true,
    returnArray = false,
  } = options || {};

  const formatNumStr = (num: number) => {
    let numStr = String(num);

    if (handleDecimal) {
      numStr = num.toFixed(decimal);
    }

    if (removeDecimalZero) {
      numStr = String(parseFloat(numStr));
    }

    return numStr;
  };

  const addThousands = (intStr: string) => {
    if (thousands) {
      return intStr.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    }
    return intStr;
  };

  const handleDecimalStr = (decimalStr: string) => {
    if (decimalStr) return `.${decimalStr}`;
    return decimalStr;
  };

  if (shortFormat) {
    if (realNum >= 10000 && realNum < 1e8) {
      const numStr = formatNumStr(realNum / 1e4);
      const [intStr, decimalStr = ''] = numStr.split('.');
      const result = `${intStr}${handleDecimalStr(decimalStr)}`;
      return returnArray ? [result, '万'] : `${result}万`;
    }
    if (realNum >= 1e8) {
      const numStr = formatNumStr(realNum / 1e8);
      const [intStr, decimalStr = ''] = numStr.split('.');
      const result = `${intStr}${handleDecimalStr(decimalStr)}`;
      return returnArray ? [result, '亿'] : `${result}亿`;
    }
  }

  const numStr = formatNumStr(realNum);
  const [intStr, decimalStr = ''] = numStr.split('.');
  const result = `${addThousands(intStr)}${handleDecimalStr(decimalStr)}`;
  return returnArray ? [result, ''] : result;
};

// 检测数字是否超出1亿
export const checkBeyondBillion = (num: number | undefined): boolean => {
  if (typeof num !== 'number') return false;
  return num >= 1e8;
};

// 检测数字是否超出1千万
export const checkBeyondTenMillion = (num: number | undefined): boolean => {
  if (typeof num !== 'number') return false;
  return num >= 1e7;
};

/* 格式化数字，仅仅返回单位，不返回数值
 */
export const formatNumberUnit = (num: number): string => {
  if (num >= 1e3 && num < 9999) {
    return '|千';
  }
  if (num >= 1e4 && num < 99999) {
    return '|万';
  }
  if (num >= 1e5 && num < 999999) {
    return '|十万';
  }
  if (num >= 1e6 && num < 9999999) {
    return '|百万';
  }
  if (num >= 1e7 && num < 99999999) {
    return '|千万';
  }
  if (num >= 1e8 && num < 999999999) {
    return '|亿';
  }
  return '';
};

// 分转元 保留2位小数
export const formatCentToYuanAndTwoDecimal = (num: number | string) => {
  num = Number(num);
  if (isNaN(num)) {
    return '-';
  }
  return (num / 100).toFixed(2);
};

/**
 * 宽度不足时的金额格式化方案
 * 不足万时，完整显示，千分位分隔；
 * 超过万时，1-99万，保留小数点后一位，为0则不显示；
 * 超过100万时，仅保留整数；
 * 亿以上的不做特殊处理。
 * @param num
 * @returns {string[]} 格式化数组，第一位是￥，第二位是数字，第三位是单位，如果没有则为空
 */
export const formatPriceWithLimitWidthAndUnit = (num: number) => {
  return ['￥', ...formatNumberWithLimitWidth(convertAmountDigitToYuan(num))];
};

/**
 * 格式化数字
 * 1. 小于万：具体数字 sep && 千分位分割
 * 2. 大于万：以万为单位；精确到小数点后1位，如:1万、1.2万
 * 3. 大于亿：以亿为单位；精确到小数点后1位，如:1亿、1.2亿
 * 4. 删除非有效位0
 * 5. 数字为0时，数字显示0；
 * 6. 数字小于0时，只是前面多了个负号
 * @param num
 * @returns [符号位, 数字, 单位]
 */
export function enhancedNumberWithLimitWidth(num: number, sep = true): [string, string, string] {
  if (num === undefined) return ['', '0', ''];
  if (num >= 0 && num <= 9999) {
    return ['', sep ? numFormat(num) : num.toString(), ''];
  } else if (num > 9999 && num < 1e8) {
    return ['', Number((num / 1e4).toFixed(1)).toString(), '万'];
  } else if (num >= 1e8) {
    return ['', Number((num / 1e8).toFixed(1)).toString(), '亿'];
  }
  const result = enhancedNumberWithLimitWidth(Math.abs(num));
  return ['-', result[1], result[2]];
}
