import React from 'react';
import { transferPriceToArray } from '@global/utils/number/numeralChain';
import styles from './index.module.scss';
export interface TieredCosStruct {
  tiered_cos_list?: {
    cos_fee: number;
  }[]; // 阶梯佣金,0:base， 1:top
  threshold?: number[]; // 阶梯佣金门槛
  start_time?: string; // 开始时间
  end_time?: string; // 结束时间
  plan_id?: number; // 阶梯计划Id
}
interface Props {
  tiered_cos?: TieredCosStruct;
  children: React.ReactNode;
}

const Tip: React.FC<Props> = (props: Props) => {
  const { children, tiered_cos } = props;

  const maxCos = tiered_cos?.tiered_cos_list?.[1]?.cos_fee;

  return (
    <div className={styles.tip}>
      {maxCos && <div className={styles.content}>最高{transferPriceToArray(maxCos / 100)}</div>}
      {children}
    </div>
  );
};

export default Tip;
