import React from 'react';

type TagFix = string | JSX.Element | JSX.Element[];
interface IIndicator {
  value: string | number;
  prefix?: TagFix;
  suffix?: TagFix;
}
function Indicator(props: IIndicator) {
  const { prefix, value, suffix } = props ?? {};
  return (
    <div>
      {prefix ? <span>{prefix}</span> : <></>}
      <span>{value ? value : '-'}</span>
      {suffix && value ? <span>{suffix}</span> : <></>}
    </div>
  );
}

export default React.memo(Indicator);
