import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';

import { Tooltip } from 'antd';
import cx from 'classnames';

import { countLines } from './utils/dom';

import styles from './index.module.scss';

export interface IProps {
  maxWidth?: number | string; // 最大宽度：支持px和百分比，不需要可以不传(比如flex布局下的元素)
  content?: string | React.ReactNode; // 内容
  className?: string; // 自定义类名
  customStyle?: React.CSSProperties; // 自定义样式
  line?: number; // 超出x行再展示省略号
  extendContents?: React.ReactNode[]; // 结尾tags组件数组
  extendPlacement?: 'topRight' | 'bottomRight'; // tag的位置
  expandTop?: number | string; // 自定义调整tag上下位置
  noEllipsisExtend?: React.ReactNode[]; // 不省略时的tags
  contentClick?: () => void; // 点击文字触发事件
}

export const EllipsisWithTags: React.FC<IProps> = (props: IProps) => {
  const [isFold, setIsFold] = useState(false);
  const [tagHeight, setTagHeight] = useState('');
  const [contentLine, setContentLine] = useState<number>(1);
  const {
    maxWidth,
    content = '',
    className = '',
    customStyle = {},
    line = 1,
    extendContents,
    expandTop,
    contentClick,
    noEllipsisExtend,
  } = props;
  let { extendPlacement = 'bottomRight' } = props;
  if (line === 1) extendPlacement = 'bottomRight';

  const extendContentsAfter = noEllipsisExtend ? noEllipsisExtend : extendContents;
  const ellipsisRef = useRef<HTMLDivElement>(null);
  const tagsRef = useRef<HTMLDivElement>(null);

  const calcDomInfo = useCallback(() => {
    let result = false;
    if (line === 1) {
      /** 单行 */
      const width = ellipsisRef?.current?.clientWidth;
      const scrollWidth = ellipsisRef?.current?.scrollWidth;
      // 兼容问题：不超出的情况下，scrollWidth和cließntWidth也不一定一致，可能有1px左右误差
      result = Boolean(scrollWidth && width && scrollWidth - width >= 1);
    } else {
      const height = ellipsisRef?.current?.clientHeight;
      const scrollHeight = ellipsisRef?.current?.scrollHeight;
      // 兼容问题：不超出的情况下，scrollHeight和clientHeight也不一定一致，可能有1px左右误差
      result = Boolean(scrollHeight && height && scrollHeight - height >= 1);
      ellipsisRef?.current && setContentLine(countLines(ellipsisRef?.current));
    }
    setIsFold(result);
  }, [ellipsisRef.current, line, content, extendContents]);

  useEffect(() => {
    calcDomInfo();
  }, [calcDomInfo]);

  useEffect(() => {
    window.addEventListener('resize', calcDomInfo);
    return () => {
      window.removeEventListener('resize', calcDomInfo);
    };
  }, [calcDomInfo]);

  const getTagHeight = useCallback(() => {
    let height: string;
    const _tagHeight = tagsRef?.current?.clientHeight || 0;
    const _totalHeight = ellipsisRef?.current?.clientHeight || 0;
    const cal = (_totalHeight / line - _tagHeight) / 2;

    if (expandTop) {
      height = `${expandTop}px`;
    } else if (extendPlacement === 'topRight' || !isFold) {
      height = `${cal}px`;
    } else {
      height = `calc(100% - ${cal + _tagHeight}px)`;
    }
    setTagHeight(height);
    return height;
  }, [expandTop, extendPlacement, isFold, line]);

  const getEllipsisStyle = useCallback(() => {
    let totalStyle = {};
    if (maxWidth) totalStyle = { maxWidth };
    if (line === 1 && !isFold) totalStyle = { ...totalStyle, display: 'flex' }; // 单行无省略垂直居中
    else if (line > 1) {
      const lineStyle = { WebkitLineClamp: line };
      totalStyle = { ...totalStyle, ...lineStyle };
    }
    return { ...totalStyle, ...customStyle };
  }, [customStyle, isFold, line, maxWidth]);

  const moreLineHideTagBefore = useMemo(() => {
    if (line === 1) return !isFold && extendPlacement !== 'topRight';
    else if (extendPlacement === 'topRight') {
      return contentLine === 1;
    }
    return !isFold;
  }, [line, extendPlacement, isFold, contentLine]);

  useEffect(() => {
    content && getTagHeight();
  }, [content, getTagHeight]);

  return (
    <div className={styles.ellipsisWithTags}>
      <div
        ref={ellipsisRef}
        className={cx(line === 1 ? styles.ellipsis : styles.ellipsisMultiLine, styles.ellipsisWithTags_Content)}
        style={getEllipsisStyle()}>
        <span className={styles.before} style={{ height: tagHeight }} />
        <span className={styles.ellipsisWithTags_TagBefore} hidden={moreLineHideTagBefore}>
          {extendContents?.map((tag, index) => {
            return <div key={index}>{tag}</div>;
          })}
        </span>
        {isFold ? (
          <Tooltip title={content} placement="topLeft">
            <span className={cx(Boolean(contentClick) ? styles.onClick : undefined, className)} onClick={contentClick}>
              {content}
            </span>
          </Tooltip>
        ) : (
          <span className={cx(Boolean(contentClick) ? styles.onClick : undefined, className)} onClick={contentClick}>
            {content}
          </span>
        )}
        <span
          ref={tagsRef}
          className={styles.ellipsisWithTags_TagAfter}
          style={!moreLineHideTagBefore ? { visibility: 'hidden' } : { top: expandTop }}>
          {extendContentsAfter?.map((tag, index) => {
            return <div key={index}>{tag}</div>;
          })}
        </span>
      </div>
    </div>
  );
};

export default EllipsisWithTags;
