import React from 'react'
import {
  goodsColumn,
  associationLiveNum,
  associationVideoNum,
  commission_rate,
  cooperationGoods,
  goodsGrade,
  goodsReviewRate,
  merchantExpScore,
  monthOrderRate,
  priceRange,
  serviceRange,
  talentPromotion,
  totalMonthSales,
  // totalSales,
  // unionMonthSales,
  // unionSales,
  viewNum,
} from './columns/public';
import { ColumnProps } from 'antd/es/table';
interface ISelCondition {
  initNotSelecteds: string[];
  notSelectedIndi: string[];
  selectedVals: string[];
}
export function GetColumns<T>(operation: ColumnProps<T>) {
  const indiColumns = [
    goodsColumn,
    priceRange,
    commission_rate,
    serviceRange,
    totalMonthSales,
    viewNum,
    talentPromotion,
    cooperationGoods,
    associationLiveNum,
    associationVideoNum,
    monthOrderRate,
    goodsGrade,
    merchantExpScore,
    goodsReviewRate,
  ];
  return React.useMemo(() => {
    return [...indiColumns, operation];
  }, []);
}
