import { Button, Col, Form, Input, Row, Slider, Divider } from 'antd';
import React, { useState } from 'react';

import s from './search-form.m.scss';

const formItemLayout = {
  labelCol: {
    xs: { span: 4 },
    sm: { span: 4 },
  },
  wrapperCol: {
    xs: { span: 18 },
    sm: { span: 4 },
  },
};

export type TValues = {
  [key: string]: any;
}
const gutters: Record<PropertyKey, number> = {};
const vgutters: Record<PropertyKey, number> = {};
const colCounts: Record<PropertyKey, number> = {};

[4, 8, 16, 24, 32, 40, 48].forEach((value, i) => {
  gutters[i] = value;
});
[0, 4, 8, 16, 24, 32, 40, 48].forEach((value, i) => {
  vgutters[i] = value;
});
[2, 3, 4, 6].forEach((value, i) => {
  colCounts[i] = value;
});
interface ISearchForm {
  searchCallback: (values: TValues) => void;
  onReset?: () => void;
}
export function SearchForm(props: ISearchForm) {
  const { searchCallback, onReset } = props;

  const [gutterKey, setGutterKey] = useState(0);
  const [vgutterKey, setVgutterKey] = useState(0);
  const [colCountKey, setColCountKey] = useState(1);
  const [roleMgtForm] = Form.useForm();
  const onFinish = (values: TValues) => {
    console.log('values', values);
  }
  const onSearch = () => {
    roleMgtForm.validateFields().then(values => {
      searchCallback?.(values);
    });
  };
  const _onReset = onReset ? onReset : () => {
    roleMgtForm.resetFields();
  }
  const formItems = [
    <Form.Item
      name="user"
      label="user"
    >
      <Input placeholder='username or userID' />
    </Form.Item>,
    <Form.Item
      name="email"
      label="E-mail"
    >
      <Input placeholder='input id' />
    </Form.Item>,
    <Form.Item
      name="role"
      label="role"
    >
      <Input placeholder='input id' />
    </Form.Item>,
    <Form.Item
      name="auth"
      label="auth"
    >
      <Input placeholder='input id' />
    </Form.Item>,
    <Form.Item
      name="page"
      label="page"
    >
      <Input placeholder='input id' />
    </Form.Item>,
    <Form.Item
      name="operation"
      label="operation"
    >
      <Input placeholder='input id' />
    </Form.Item>,
    <Form.Item
      name="cell"
      label="cell"
    >
      <Input placeholder='input id' />
    </Form.Item>
  ]
  const colCount = colCounts[colCountKey];
  function getColsByCnt(_colCnt: number, idx: number) {
    const cols: React.JSX.Element[] = [];
    for (let i = 0;i < _colCnt;i++) {
      const _index = (idx) * colCount + i;
      const colEle = (
        <Col key={_index.toString()} span={24 / colCount}>
          <div>
            {formItems?.[_index]}
          </div>
        </Col>
      );
      cols.push(colEle);
    }
    return cols;
  }

  function getRowLayout() {
    const formItemNum = formItems.length;
    const resColNum = formItemNum % colCount;
    const rowNum = (formItemNum - resColNum) / colCount;
    const eleCols: React.JSX.Element[] = [];
    new Array(rowNum).fill(0).forEach((_, idx: number) => {
      if (idx === rowNum) {
        eleCols.push(...getColsByCnt(resColNum, idx));
      } else {
        eleCols.push(...getColsByCnt(colCount, idx));
      }
    });
    // console.log('cols', rowNum, resColNum, eleCols)
    return (
      <Row gutter={[gutters[gutterKey], vgutters[vgutterKey]]}>
        {eleCols}
      </Row>
    )
  }
  return (
    <div className={s.wrap_search}>
      <div style={{ width: '50%', marginBottom: 48 }}>
        <Slider
          min={0}
          max={Object.keys(colCounts).length - 1}
          value={colCountKey}
          onChange={setColCountKey}
          marks={colCounts}
          step={null}
          tooltip={{ formatter: (value: number) => colCounts[value] }}
        />
      </div>
      <Form
        // {...formItemLayout}
        labelCol={{
          xs: { span: 4 },
          sm: { span: 4 },
        }}
        form={roleMgtForm}
        onFinish={onFinish}
        scrollToFirstError
        className={s.form}
      >
        <section className={s.filter_cell}>
          {getRowLayout()}
        </section>

        <Divider type={'vertical'} className={s.divider_cell} style={{ height: 88 }} />
        <section className={s.ctrl_cell}>
          <Form.Item>
            <Button
              key="search-key"
              htmlType="submit"
              type={'primary'}
              onClick={onSearch}
            >
              <span>search</span>
            </Button>
          </Form.Item>
          <Form.Item>
            <Button
              key="reset-key"
              onClick={_onReset}
            >
              <span>reset</span>
            </Button>
          </Form.Item>
        </section>

      </Form>
    </div>
  )
}
