import { atom } from "recoil";
import { GetSelectionProductItem } from "./type";
import { getSelectGoods } from "./apis";

export const productAtom = atom<Array<GetSelectionProductItem>>({
  key: 'productAtom',
  default: (() => {
    const res: any = getSelectGoods()
    return res?.data?.product_list
  })() // 返回 promise
});

export const sedProdsAtom = atom<Array<string>>({
  key: 'sedProdsAtom',
  default: [],
});
