import React, { useState } from 'react';

import { Button } from 'antd';
import Icon from '@ant-design/icons';
import { ButtonType } from 'antd/es/button';

import { ESetColumnsShowType, IIndicatorTreeData, ISelectedIndicator } from './types';

import SetColumnsDrawer, { ISetColumnsDrawer } from './components/SetColumnsDrawer';
import SetColumnsModal, { ISetColumnsModal } from './components/SetColumnsModal';
import svgSetColumnsSetting from './icons/set-columns-setting.svg';

// import './styles/variable.less';
import './styles/index.less';
// import './styles/draggableOrder.less';
// import './styles/setColumnsDrawer.less';
// import './styles/setColumnsModal.less';


export type {
  ButtonType,
  ESetColumnsShowType,
  IIndicatorTreeData,
  ISelectedIndicator,
  ISetColumnsDrawer,
  ISetColumnsModal,
};

export interface ISetColumns {
  /**
   * 按钮名称
   */
  buttonName?: string;
  /**
   * 按钮展示模式（默认primary）
   */
  buttonType?: ButtonType;
  /**
   * 需要展示的自定义表头类型
   */
  setColumnType?: ESetColumnsShowType;
  /**
   * 是否隐藏选择个数展示
   */
  isSelectedNumHidden?: boolean;
  /**
   * 是否展示全选
   */
  showSelectAll?: boolean;
  /**
   * 默认可选择区块数据
   */
  defaultTreeData: IIndicatorTreeData[];
  /**
   * 已选择表格列list
   */
  defaultSelectedList: ISelectedIndicator[];
  /**
   * 点击确定后，最终设置展示的已选择列和treeData数据
   */
  updateViewDetail?: (selectedIndicator: ISelectedIndicator[], curIndicatorTreeData: IIndicatorTreeData[]) => void;
}

export const SetColumns = (props: ISetColumns) => {
  const [columnsShow, setColumnsShow] = useState<boolean>(false);

  const {
    buttonName = '指标配置',
    buttonType,
    setColumnType = 'drawer',
    isSelectedNumHidden,
    defaultTreeData,
    defaultSelectedList,
    showSelectAll,
    updateViewDetail,
  } = props;

  return (
    <div>
      <Button
        type={buttonType}
        icon={<Icon component={svgSetColumnsSetting} />}
        onClick={() => {
          setColumnsShow(true);
        }}>
        {buttonName}
      </Button>
      {setColumnType === 'drawer' && (
        <SetColumns.Drawer
          visible={columnsShow}
          showSelectAll={showSelectAll}
          isSelectedNumHidden={isSelectedNumHidden}
          onVisibleChange={visible => setColumnsShow(visible)}
          updateViewDetail={updateViewDetail}
          defaultSelectedList={defaultSelectedList}
          defaultTreeData={defaultTreeData}
        />
      )}
      {setColumnType === 'modal' && (
        <SetColumns.Modal
          visible={columnsShow}
          showSelectAll={showSelectAll}
          isSelectedNumHidden={isSelectedNumHidden}
          onVisibleChange={visible => setColumnsShow(visible)}
          updateViewDetail={updateViewDetail}
          defaultSelectedList={defaultSelectedList}
          defaultTreeData={defaultTreeData}
        />
      )}
    </div>
  );
};

SetColumns.Drawer = SetColumnsDrawer;
SetColumns.Modal = SetColumnsModal;

export default SetColumns;
